import { LocationService } from 'services/location.service';
import { Component, OnInit, NgZone, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/switchMap';
import { } from 'googlemaps';
import { GoogleMapsService } from "services/google-maps-service.service";
import { MapsAPILoader } from "@agm/core";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  searchForm: FormGroup;
  title = 'Mapocation';
  locations = [];
  iconUrl = "/assets/map_pink.png";
  place = "taxila";
  resultTitle = 'no results';
  foundPlaces;


  private latitude = 30.935432;
  private longitude = 74.8746008;
  private zoom = 6;
  mainImageUrl = '';

  @ViewChild("search")
  public searchElementRef: ElementRef;
  
  constructor(
    private mapService: GoogleMapsService,
    private _locationService: LocationService,
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone
  ) {
    this.searchForm = new FormGroup({
      '_address': new FormControl()
    });

  }

  ngOnInit() {
    // this.searchForm.valueChanges
    //   .debounceTime(200)
    //   .switchMap(value => this.mapService.getCoordinates(value._address))
    //   .subscribe((results) => {
    //     if (this.searchForm.controls['_address'].value) {
    //       this.resultTitle = results.results[0].formatted_address;
    //       this._locationService.getIntroFromWikPedi(results.results[0].geometry.location.lat, results.results[0].geometry.location.lng)
    //       .subscribe((res) => {
    //         console.log(res);
    //       })
    //     }
    //   })


      //load Places Autocomplete
    this.mapsAPILoader.load().then(() => {
      let autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement, {
        types: ["address"]
      });
      autocomplete.addListener("place_changed", () => {
        this.ngZone.run(() => {
          //get the place result
          let place: google.maps.places.PlaceResult = autocomplete.getPlace();

          //verify result
          if (place.geometry === undefined || place.geometry === null) {
            return;
          }
          this._locationService.getPlaces(place.geometry.location.lat(), place.geometry.location.lng())
          .subscribe((res) => {
            if(res) {
              console.log(res.results);
              this.foundPlaces = res.results;
            }
          })
          //set latitude, longitude and zoom
          this.resultTitle = place.name;
          this.latitude = place.geometry.location.lat();
          this.longitude = place.geometry.location.lng();
          this.zoom = 12;
        });
      });
    });
  }


}



