import { GoogleMapsService } from 'services/google-maps-service.service';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AgmCoreModule } from '@agm/core';
import { AgmSnazzyInfoWindowModule } from '@agm/snazzy-info-window';
import { AppComponent } from './app.component';
import { ReactiveFormsModule } from "@angular/forms";
import { HttpModule } from "@angular/http";
import { LocationService } from "services/location.service";
import { GoogleMapsKey } from "app/google.maps.key";

@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    BrowserModule,
    ReactiveFormsModule,
    HttpModule,
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyCqzLJPg70Gq7QZe3gw3lI0IPwSPB2ytg0',
      libraries: ["places"]
    }),
    AgmSnazzyInfoWindowModule
  ],
  providers: [GoogleMapsService, LocationService, GoogleMapsKey],
  bootstrap: [AppComponent]
})
export class AppModule { }



