import { Http, RequestOptions, Headers } from '@angular/http';
import { Injectable } from '@angular/core';
import { GoogleMapsKey } from "app/google.maps.key";

@Injectable()
export class LocationService {



    headerPhotoUrl = "http://localhost:3000/getHeaderPhoto"; 
    getPlacesUrl = "https://berlin-location.herokuapp.com/getPlaces";
    constructor(private _http: Http, private _googleMapsKey: GoogleMapsKey) { }

    getHeaderPhoto(placeId) {
        let headers = new Headers();
        headers.append('Content-Type', 'application/json; charset=UTF-8');
        
        var options = new RequestOptions({
            headers: headers,
            params: {
                placeId: placeId
            }
        });

        return this._http.get(`${this.headerPhotoUrl}`, options)
        .map((res) => {
            return res.json();
        })
    }

    getPlaces(lat, lng) {
        let headers = new Headers();
        headers.append('Content-Type', 'application/json; charset=UTF-8');
        
        var options = new RequestOptions({
            headers: headers,
            params: {
                lat, lng
            }
        });

        return this._http.get(`${this.getPlacesUrl}`, options)
        .map((res) => {
            return res.json();
        })
    }
}


